ory-header {\n    font-family: var(--font-headers);\n    margin-bottom: 0.5rem;\n    font-size: 1.6rem;\n}\n\n.cat-button {\n    background-color: inherit;\n    border: none;\n    cursor: pointer;\n    display: flex;\n    text-align: left;\n    font-family: var(--font-headers);\n    font-size: 1.4rem;\n}\n\n.cat-button:hover {\n    font-weight: 600;\n}\n\n#generated-cat-buttons {\n    display: flex;\n    flex-direction: column;\n}\n\n.cat-button-container {\n    display: flex;\n    align-items: center;\n}\n\n.arrow-icon {\n    width: 1rem;\n    height: 1rem;\n}\n\n/* To do container */\n.todo-display {\n    width: 40rem;\n    min-height: 25rem;\n    margin-left: 1rem;\n    margin-bottom: 3rem;\n    display: flex;\n    flex-direction: column;\n}\n\n.todo-item-container,\n.todo-button-container {\n    display: grid;\n    grid-template-rows: 3rem;\n    grid-auto-rows: minmax(3rem, auto);\n}\n\n.todo-item,\n.todo-add-todo,\n.todo-add-cat,\n.todo-form {\n    border-bottom: 1px solid rgba(0, 0, 0, 0.3); \n    display: flex;\n    align-items: end;\n    gap: 1rem;\n    height: 3rem;\n}\n\n.todo-form-title,\n.cat-form-title {\n    background-color: inherit;\n    outline: none;\n    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);\n    margin-left: 3rem;\n    padding-left: 1rem;\n    border-radius: 8px;\n    color: rgba(0, 0, 0, 0.5);\n    font-size: 0.8rem;\n}\n\n.todo-form-title:focus,\n.cat-form-title:focus {\n    outline: none;\n    border: none;\n}\n\n.cat-display {\n    height: 100%;\n    font-family: var(--font-headers);\n    font-size: 2rem;\n    font-weight: 600;\n    padding-top: 0.7rem;\n    cursor: text;\n}\n\n.cat-due {\n    font-size: 0.7rem;\n    color: rgba(0, 0, 0, 0.6);\n}\n\n.checkbox {\n    width: 1rem;\n    height: 1rem;\n    border: 2px solid #333;\n    cursor: pointer;\n    display: inline-block;\n    vertical-align: middle;  \n    position: relative;      \n    padding: 0;   \n    margin-bottom: 0.3rem;\n    margin-left: 1rem;           \n    border-radius: 50%;\n}\n\n.checkbox:hover {\n    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);\n}\n\n.checkbox[aria-pressed=\"true\"] {\n    background-color: #333; \n    border-color: #333;\n}\n\n.todo-display-header {\n    height: 100%;\n    padding-top: 1.4rem;\n    cursor: text;\n    color: rgba(0, 0, 0, 0.6);\n    font-family: var(--font-headers);\n    font-size: 1.3rem;\n    font-style: italic;\n}\n\n.todo-due {\n    font-size: 0.7rem;\n    color: rgba(0, 0, 0, 0.6);\n}\n\n.cat-edit-button,\n.cat-restore-button,\n.cat-submit-button,\n.cat-close-button,\n.todo-close-button,\n.todo-edit-button,\n.todo-restore-button {\n    background-color: inherit;\n    border: none;\n    cursor: pointer;\n    margin-bottom: 0.3rem;\n}\n\n.cat-delete-button,\n.todo-delete-button {\n    background-color: inherit;\n    border: none;\n    cursor: pointer;\n    margin-bottom: 0.3rem;\n}\n\n.edit-icon,\n.delete-icon,\n.close-icon,\n.restore-icon {\n    width: 1.3rem;\n    height: 1.3rem;\n}\n\n.edit-icon:hover,\n.delete-icon:hover,\n.close-icon:hover,\n.restore-icon:hover {\n    border-radius: 3px;\n    box-shadow: 0px 0px 8px rgba(196, 139, 4, 0.8), inset 0px 0px 20px rgba(196, 139, 4, 0.8);\n}\n\n.submit-icon {\n    width: 1.6rem;\n    height: 1.6rem;\n}\n\n.add-todo,\n.add-cat {\n    background-color: inherit;\n    border: none;\n    cursor: pointer;\n    margin-left: 2.7rem;\n    font-size: 0.8rem;\n    color: rgba(0, 0, 0, 0.3);\n}\n\n.add-todo:hover,\n.add-cat:hover {\n    color: rgba(0, 0, 0, 0.5);\n}\n\n.hidden {\n    display: none;\n}\n\n/* Edit Forms */\n/* Edit Cat Form */\n.edit-cat-form {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-template-rows: 3rem;\n    grid-auto-rows: minmax(3rem, auto);\n    margin-top: 1rem;\n    gap: 0.2rem;\n    background-color: var(--bg-lightest);\n}\n\n#edit-cat-title,\n#edit-cat-due,\n#edit-cat-description {\n    background-color: white;\n    border: none;\n    border-radius: 8px;\n    padding-left: 1rem;\n}\n\n#edit-cat-title:hover,\n#edit-cat-due:hover,\n#edit-cat-description:hover {\n    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);\n}\n\n#edit-cat-title:focus,\n#edit-cat-due:focus,\n#edit-cat-description:focus {\n    outline: none;\n    box-shadow: 0px 0px 8px var(--bg-darker);\n}\n\n#edit-cat-title:hover:focus,\n#edit-cat-due:hover:focus,\n#edit-cat-description:hover:focus {\n    outline: none;\n    box-shadow: 0px 0px 8px var(--bg-darker);\n}\n\n#edit-cat-title {\n    grid-area: 1 / 1 / 2 / 2;\n}\n\n#edit-cat-due {\n    grid-area: 1 / 2 / 2 / 3;\n    cursor: pointer;\n}\n\n#edit-cat-description {\n    grid-area: 2 / 1 / 3 / 3;\n}\n\n.cat-submit-button {\n    grid-area: 3 / 1 / 4 / 3;\n    background-color: inherit;\n    border: 1px solid black;\n    border-radius: 8px;\n    cursor: pointer;\n    margin-top: 1rem;\n    margin-bottom: 1rem;\n    padding: 0.5rem 2rem;\n}\n\n.cat-submit-button:hover {\n    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);\n    font-weight: bold;\n}\n\n/* Edit todo forms */\n.edit-todo-form {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-template-rows: 3rem;\n    grid-auto-rows: min-max(3rem, auto);\n    margin-top: 1rem;\n    gap: 0.2rem;\n    background-color: var(--bg-lightest);\n}\n\n#edit-todo-title,\n#edit-todo-due,\n#edit-todo-description,\n#edit-todo-cat,\n#edit-priority,\n#notes,\n#when-created {\n    background-color: white;\n    border: none;\n    border-radius: 8px;\n    padding-left: 1rem;\n    height: 3rem;\n}\n\n#edit-todo-title:focus,\n#edit-todo-due:focus,\n#edit-todo-description:focus,\n#edit-todo-cat:focus,\n#edit-priority:focus,\n#notes:focus {\n    outline: none;\n    box-shadow: 0px 0px 8px var(--bg-darker);\n}\n#edit-todo-title:hover,\n#edit-todo-due:hover,\n#edit-todo-description:hover,\n#edit-todo-cat:hover,\n#edit-priority:hover,\n#notes:hover,\n#when-created:hover {\n    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);\n}\n\n#edit-todo-title:hover:focus,\n#edit-todo-due:hover:focus,\n#edit-todo-description:hover:focus,\n#edit-todo-cat:hover:focus,\n#edit-priority:hover:focus,\n#notes:hover:focus,\n#when-created:hover:focus {\n    box-shadow: 0px 0px 8px var(--bg-darker);\n}\n\n#edit-todo-title {\n    grid-area: 1 / 1 / 2 / 2;\n}\n\n#edit-todo-due {\n    grid-area: 1 / 2 / 2 / 3;\n    cursor: pointer;\n}\n\n#edit-todo-description {\n    grid-area: 2 / 1 / 3 / 3;\n}\n\n#edit-todo-cat {\n    grid-area: 3 / 1 / 4 / 2;\n    cursor: pointer;\n}\n\n#edit-priority {\n    grid-area: 3 / 2 / 4 / 3;\n    cursor: pointer;\n}\n\n#notes {\n    grid-area: 4 / 1 / 5 / 3;\n}\n\n#when-created {\n    grid-area: 5 / 1 / 6 / 3;\n}\n\n.todo-submit-button {\n    grid-area: 6 / 1 / 7 / 3;\n    background-color: inherit;\n    border: 1px solid black;\n    border-radius: 8px;\n    cursor: pointer;\n    margin-top: 1rem;\n    margin-bottom: 1rem;\n    padding: 0.5rem 2rem;\n}\n\n.todo-submit-button:hover {\n    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);\n    font-weight: bold;\n}\n\n/* Footer Styling */\nfooter {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding-bottom: 2rem;\n  }\n  \n  .credit {\n    display: flex;\n    gap: 0.3rem;\n  }\n  \n  footer a {\n    text-decoration: none;\n    color: var(--text-darkest);\n    transition: color 0.3s ease; \n  }\n  \n  footer a:hover {\n    color: var(--text-light);\n    }\n  \n  .credit img {\n    height: 1.2rem;\n    width: 1.2rem;\n  }\n"],"sourceRoot":""}]);
